import { useState, useMemo, useEffect, useCallback } from "react";
import styled from "styled-components";
import Styles from "../../../../base/design/styles";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import DrawUtil from "../../../graphic/drawUtil";
import LogicElementUtil from "../../factory/util/logicElementUtil";
import LogicUtil from "../../factory/util/logicUtil";
import PreviewUtil from "../previewUtil";

const PreviewSeedInputForm = (props: {
    seedWrap: LogicUtil.WrapElement;
    setSendingValueMap: (sendingValueMap: (() => PreviewUtil.ValueMap[]) | null) => void;
}) => {

    const [formList, setFormList] = useState<FormUtil.CheckableValue[]>([]);
    const [template, setTemplate] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const [formgrpList, argumentList, presetList]: [LogicUtil.NodeFromGrpItem[], LogicUtil.NodeSeedArgItem[], LogicUtil.NodeSampleItem[]] = useMemo(() => {
        const formgrpWrap = LogicElementUtil.getInnerWrap(props.seedWrap, 'interface-mng', 'formgrp-mng');
        let formgrpList: LogicUtil.NodeFromGrpItem[] = [];
        if (formgrpWrap != null) {
            formgrpList = (formgrpWrap.data as LogicUtil.NodeFromgrpManager).defines.map(def => def.data as LogicUtil.NodeFromGrpItem);
        }

        const argWrap = LogicElementUtil.getInnerWrapFixed(props.seedWrap, 'interface-mng', 'seedarg-mng');
        const argumentList = (argWrap.data as LogicUtil.NodeSeedArgManager).args.map(arg => arg.data as LogicUtil.NodeSeedArgItem);

        const sampleWrap = LogicElementUtil.getInnerWrapFixed(props.seedWrap, 'sample-mng');
        const sampleList = (sampleWrap.data as LogicUtil.NodeSampleManager).samples.map(wrap => wrap.data as LogicUtil.NodeSampleItem);
        return [formgrpList, argumentList, sampleList];
    }, []);


    useEffect(() => {
        setFormList(argumentList.map(arg => {
            let value = '';
            if (arg.defval != undefined) value = arg.defval.toString();
            return { value, errors: [] };
        }));
    }, []);

    useEffect(() => {
        if (formList.find(form => form.errors.length > 0) == undefined) {
            props.setSendingValueMap(() => {
                const valueMap = formList.map((form, i) => {
                    return { name: argumentList.map(arg => arg.name)[i], value: Number(form.value) }
                });

                // SeedValueMapSendOrigin
                const sendMng = LogicElementUtil.getInnerWrapFixed(props.seedWrap, 'interface-mng', 'seedsend-mng').data as LogicUtil.NodeSeedSendManager;
                const sendParams = sendMng.params.map(send => send.data as LogicUtil.NodeParamItem);
                return sendParams.map(item => {
                    const value = PreviewUtil.convertParamsToValue(item.name, sendParams, valueMap);
                    if (value == undefined) throw new Error(`[${item.name}]Send`);
                    return { name: item.name, value }
                });
            });
        } else {
            props.setSendingValueMap(null);
        }
    }, [formList]);

    const buildForms = (): JSX.Element[] => {
        const list: JSX.Element[] = [];
        const usedGrps: string[] = [];
        formList.forEach((form, i) => {
            const argument = argumentList[i];
            const formgrp = formgrpList.find(grp => grp.args.includes(argument.name));
            if (formgrp != undefined) {
                // 
                if (usedGrps.includes(formgrp.name)) return 1;
                // 
                usedGrps.push(formgrp.name);
                const targetForms: number[] = [];
                argumentList.forEach((arg, j) => {
                    if (formgrp.args.includes(arg.name)) {
                        targetForms.push(j);
                    }
                });
                const colorCode = DrawUtil.convertColorCodeFromRgba({
                    r: Number(formList[targetForms[0]].value),
                    g: Number(formList[targetForms[1]].value),
                    b: Number(formList[targetForms[2]].value),
                    a: 255
                }).substring(0, 7);
                // console.log(colorCode);
                list.push(<div key={i}>
                    <FormUtil.Record
                        jsx={
                            <_Message>{formgrp.name}: <_Type>color </_Type><_Range>({0}  {255} respectively)</_Range></_Message>
                        }
                    />
                    <FormUtil.Record
                        jsx={<>
                            {targetForms.map((formIndex, j) => {
                                return (
                                    <FormUtil.TextField
                                        key={formIndex}
                                        width={92}
                                        marginLeft={j === 0 ? 10 : 4}
                                        isNumber
                                        checkable={formList[formIndex]}
                                        setCheckable={() => {
                                            setFormList(formList.slice());
                                        }}
                                        validates={[
                                            {
                                                checker: (value) => ValidateUtil.checkRequired(value),
                                                errorType: "required"
                                            },
                                            {
                                                checker: (value) => ValidateUtil.checkNumberRange(value, argument.min, argument.max),
                                                errorType: 'value'
                                            }
                                        ]}
                                    />
                                );
                            })}
                            <_Color type="color" value={colorCode}
                                onChange={(value) => {
                                    const rgba = DrawUtil.convertRgbaFromColorCode(value.target.value + 'ff');
                                    if(rgba != null) {
                                        formList[targetForms[0]].value = rgba.r.toString();
                                        formList[targetForms[1]].value = rgba.g.toString();
                                        formList[targetForms[2]].value = rgba.b.toString();
                                    }
                                    setFormList(formList.slice());
                                }}
                            />
                        </>}
                    />
                </div>);
            } else {
                list.push(<div key={i}>
                    <FormUtil.Record
                        jsx={
                            <_Message>{argument.name}: <_Type>number </_Type><_Range>({argument.min}  {argument.max})</_Range></_Message>
                        }
                    />
                    <FormUtil.Record
                        jsx={<>
                            <FormUtil.TextField
                                width={152}
                                marginLeft={10}
                                isNumber
                                checkable={form}
                                setCheckable={() => {
                                    setFormList(formList.slice());
                                }}
                                validates={[
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    },
                                    {
                                        checker: (value) => ValidateUtil.checkNumberRange(value, argument.min, argument.max),
                                        errorType: 'value'
                                    }
                                ]}
                            />
                            <_Tsumami type="range" value={Number(form.value)} min={argument.min} max={argument.max}
                                onChange={(value) => {
                                    formList[i] = { ...form, value: value.target.value };
                                    setFormList(formList.slice());
                                }}
                            />
                        </>}
                    />
                </div>);
            }
        });
        return list;
    }

    return (<>
        <FormUtil.BorderPanel
            title="Preset"
            innerJsx={<FormUtil.Record
                jsx={<>
                    <FormUtil.Combobox
                        width={402}
                        checkable={template}
                        setCheckable={setTemplate}
                        headBlank
                        list={presetList.map((sample, i) => {
                            return {
                                labelText: sample.name,
                                value: i.toString()
                            }
                        })}
                    />
                    <FormUtil.ButtonItem
                        button={{
                            label: 'Apply',
                            isEnable: template.value !== '',
                            callback: () => {
                                const selectedIndex = Number(template.value);
                                const values = presetList[selectedIndex].values;
                                values.forEach((value, i) => {
                                    formList[i].value = value.toString();
                                });
                                setFormList(formList.slice());
                            }
                        }}
                    />
                </>}
            />}
        />
        {buildForms()}
    </>);
}

export default PreviewSeedInputForm;


const _Message = styled.div<{
}>`
    display: inline-block;
    height: calc(100% - 4px);
    margin-top: 2px;
    font-size: ${Styles.FONT_MIDIUM}px;
    line-height: 32px;
    /* font-weight: 600; */
    border-radius: 4px;
    padding: 0 5px;
    /* background-color: #d37878; */
    text-align: left;
    vertical-align: top;
    user-select: text;
`;

const _Type = styled.span`
    color: #c10000;
    font-weight: 600;
`;
const _Range = styled.span`
    color: #423e84fb;
`;

const _Tsumami = styled.input`
    display: inline-block;
    vertical-align: top;
    margin: 11px 0 0 10px;
    width: 400px;
`;

const _Color = styled.input`
    display: inline-block;
    vertical-align: top;
    margin: 3px 0 0 10px;
    width: 100px;
    height: 34px;
`;