import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../../utils/form/validateUtil";
import TreeUtil from "../../../../../utils/treeUtil";
import { GlobalContext } from "../../../../entry/systemEntry";
import { FactoryTreeContext } from "../../logicMainteTab";
import LogicUtil from "../../util/logicUtil";
import ModifyOperations from "../common/modifyOperations";
import LogicTemplateDialog from "../../dialog/logicTemplateDialog";
import LogicElementUtil from "../../util/logicElementUtil";

const EditorSampleItem = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { removeSelf, node, rootData, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [paramForms, setParamForms] = useState<FormUtil.CheckableValue[]>([]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeSampleItem;
        setName({ ...name, value: param.name });
        setParamForms(param.values.map(value => {
            return { value: value.toString(), errors: [] };
        }));
    };

    /**  */
    const existNameList = useMemo(() => LogicElementUtil.getExistNameList(node), [node]);

    const argInfoList: LogicUtil.NodeSeedArgItem[] = useMemo(() => {
        const argMng = LogicElementUtil.getInnerWrap((node.parent?.parent as TreeUtil.ElementNode).data, 'interface-mng', 'seedarg-mng');
        if (argMng != null) {
            return (argMng.data as LogicUtil.NodeSeedArgManager).
                args.map(arg => {
                    return arg.data as LogicUtil.NodeSeedArgItem;
                });
        } else throw new Error('argMng is undefined.');
    }, [node]);

    useEffect(() => {
        if (wrap.data == null) {
            setParamForms(argInfoList.map(arg => {
                let defval = ' ';
                if (arg.defval != undefined) defval = arg.defval.toString();
                return { value: defval, errors: [] };
            }));
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    const formList = useMemo(() => {
        return paramForms.map((paramForm, i) => {
            const arg = argInfoList[i];
            return <FormUtil.FormRecord key={i}
                titleLabel={arg.name}
                jsx={
                    <FormUtil.TextField
                        width={150}
                        isReadOnly={!isModify}
                        checkable={paramForm}
                        setCheckable={(checkable) => {
                            paramForms[i] = checkable;
                            setParamForms(paramForms.slice());
                        }}
                        isNumber
                        step={arg.step}
                        validates={[
                            {
                                checker: (value) => ValidateUtil.checkRequired(value),
                                errorType: "required"
                            },
                            {
                                checker: (value) => ValidateUtil.checkNumberRange(value, arg.min, arg.max),
                                errorType: "value"
                            },
                        ]}
                    />
                }
            />
        });
    }, [paramForms, isModify]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[name].concat(paramForms)}
            firstRegist={() => {
                const element: LogicUtil.NodeSampleItem = {
                    name: name.value,
                    values: []
                };
                paramForms.forEach((form, i) => {
                    element.values[i] = Number(form.value);
                });
                wrap.data = element;

                invalidate();
                setStore({ ...store });
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeSampleItem;
                param.name = name.value;
                paramForms.forEach((form, i) => {
                    param.values[i] = Number(form.value);
                });
                wrap.data = param;
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={250}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => !existNameList.includes(value),
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                {formList}
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    align="center"
                    buttons={[
                        {
                            label: 'Delete',
                            callback: () => {
                                removeSelf();
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicElementUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default EditorSampleItem;